<template>
  <div class="login-view">
    <el-form
      :model="userData"
      status-icon
      :rules="rules"
      ref="userData"
      class="demo-ruleForm"
      label-width="80px"
    >
      <el-form-item label="用户名" prop="username">
        <el-input
          type="text"
          v-model="userData.username"
          autocomplete="off"
          placeholder="请输入用户名"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="userData.password"
          autocomplete="off"
          placeholder="请输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('userData')"
          >提交</el-button
        >
        <el-button @click="resetForm('userData')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { login } from "../http/api";
export default {
  data() {
    return {
      userData: {
        username: "user",
        password: "7fe004e4-7c63-4de6-8019-7382f2379d93",
      },
      rules: {
        username: [
          {
            validator(rule, value, callback) {
              if (value === "") {
                callback(new Error("请输入用户名"));
              } else {
                callback();
              }
            },
            message: "请输入用户名",
            trigger: "blur",
          },
        ],
        password: [
          {
            validator(rule, value, callback) {
              if (value === "") {
                callback(new Error("请输入密码"));
              } else {
                callback();
              }
            },
            message: "请输入密码",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          login({ accesstoken: this.userData.password }).then((res) => {
            this.$message({
              type: "success",
              message: '登陆成功',
              duration: 1000
            });
            const userInfor = {
              id: res.id,
              loginname: res.loginname,
              avatar_url: res.avatar_url
            }
            // 存token 确定详情页是否需要拿到 token，登录后才有 token
            localStorage.setItem('accessToken', this.userData.password)
            localStorage.setItem('userInfor', JSON.stringify(userInfor))
            this.$store.commit('getUserInfor', userInfor)
            this.$router.push("/");
          });
        } else {
          this.$message({
            type: "error",
            message: 'token错误',
            duration: 1000
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less">
.login-view {
  min-width: 300px;
  width: 20%;
  margin: 0 auto;
  .el-form {
    margin-top: 200px;
  }
}
</style>