<template>
  <div :class="['comment', { active: item.id === maxId }]">
    <el-avatar
      shape="square"
      :size="30"
      :src="item.author.avatar_url"
    ></el-avatar>
    <div class="comment-infor">
      <div class="infor-top">
        <router-link to="">{{ item.author.loginname }}</router-link>
        <a class="time"
          >{{ index + 1 }}楼 * {{ item.create_at | moment("from") }}</a
        >
      </div>
      <p class="infor-bottom" v-html="item.content"></p>
    </div>
    <div v-if="item.ups.length" class="content">
      <span @click="handleLike" :class="['iconfont','icon-good',{isUped: true}]"></span>
      <span v-if="userInfor">{{ item.ups.length }}</span>
    </div>
  </div>
</template>

<script>
import { replyLike } from "../http/api";
export default {
  props: ["item", "maxId", "index", "userInfor"],
  methods: {
    async handleLike() {
      const accesstoken = localStorage.getItem('accessToken')
      if(accesstoken) {
        await replyLike({id: this.item.id, accesstoken})
        this.$emit('getTopic')
      } else {
        this.$message('请登录！')
      }
    }
  },

};
</script>

<style lang="less" scoped>
.comment {
  display: flex;
  border-top: 1px solid #ddd;
  padding: 0 10px 20px !important;
  padding:0 10px;
  &.active {
    background-color: antiquewhite;
  }
  &:first-child {
    border-top: none;
  }
  .comment-infor {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin-left: 10px;
    .time {
      margin-left: 10px;
    }
    .infor-bottom {
      margin-top: 5px;
      font-size: 14px;
      letter-spacing: 2px;
    }
  }
  .content {
    flex-shrink: 0;
    .icon-good {
      margin-right: 3px;
    }
  }
}
.isUped {
  color: red
}
</style>