<template>
  <div :style="`width: ${currentWidth}`" class="panel-view">
    <div v-if="$slots.header" class="panel-header">
      <slot name="header" />
    </div>
    <div class="panel-content">
      <slot name="content" />
    </div>
  </div>
</template>

<script>
export default {
  props: ["width"],
  computed: {
    currentWidth() {
      const { width } = this;
      return width * 1 ? width + "px" : width;
    },
  },
};
</script>

<style lang="less">
.panel-view {
  width: 80%;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 5px;
  overflow: hidden;
  .panel-header {
    padding: 0 10px;
    line-height: 38px;
    background-color: #ffefef;
  }
  .panel-content {
    background-color: #eeefff;
  }
}
</style>