<template>
  <div v-if="detail">
    <PanelView>
      <template #content>
        <h3>{{ detail.title }}</h3>
        <p>
          <span>{{ detail.author.loginname }}</span>
        </p>
        <div class="detail-html" v-html="detail.content"></div>
        <template>
          <div>
            <el-button @click="collect" v-if="detailIsCollect" type="success"
              >收藏</el-button
            >
            <el-button @click="deCollect" v-else type="default"
              >取消收藏</el-button
            >
          </div>
        </template>
      </template>
    </PanelView>
    <!-- 回复列表 -->
    <PanelView v-if="detail.replies.length">
      <template #header>{{ detail.replies.length }}回复</template>
      <template #content>
        <CommentsItem
          class="reply-item"
          v-for="(item, index) in detail.replies"
          :maxId="findMax"
          :key="item.id"
          :item="item"
          :index="index"
          :userInfor="userInfor"
          @getTopic="getTopic"
        />
      </template>
    </PanelView>
    <!-- 评论框 -->
    <PanelView v-if="userInfor">
      <template #header> 添加回复 </template>
      <template #content>
        <el-input
          type="textarea"
          placeholder="请输入内容"
          v-model="textarea"
          maxlength="100"
          show-word-limit
        >
        </el-input>
        <el-button @click="addComment" class="add-comment" type="success"
          >添加</el-button
        >
      </template>
    </PanelView>
  </div>
</template>

<script>
import {
  getDetail,
  collectTopic,
  deCollectTopic,
  addComment,
} from "../http/api";
import PanelView from "../components/PanelView.vue";
import CommentsItem from "../components/CommentsItem.vue";
import { mapState } from "vuex";
export default {
  components: { PanelView, CommentsItem },
  data() {
    return {
      detail: null,
      textarea: "",
      accessToken: "",
      detailIsCollect: false,
    };
  },
  props: ["id"],
  computed: {
    findMax() {
      let max = 0,
        id = "";
      this.detail.replies.forEach((reply) => {
        if (reply.ups.length > max) {
          max = reply.ups.length;
          id = reply.id;
        }
      });
      return id;
    },
    // 获取 token，因为多次使用所以写成计算属性
    getAccessToken() {
      const accessToken = localStorage.getItem("accessToken");
      return accessToken || "";
    },
    // 没有用户信息时，不显示回复框
    ...mapState(["userInfor"]),
  },
  methods: {
    // 收藏
    async collect() {
      const { id } = this.detail;
      const accesstoken = this.getAccessToken;
      await collectTopic({ topic_id: id, accesstoken });
      this.detailIsCollect = false;
      // 401 没权限
      // 500
    },
    // 取消收藏
    async deCollect() {
      const { id } = this.detail;
      const accesstoken = this.getAccessToken;
      await deCollectTopic({ topic_id: id, accesstoken });
      this.detailIsCollect = true;
    },
    // 拿到后台数据，重新发请求更新前端
    async getTopic() {
      let params = { id: this.id };
      if (this.accessToken) {
        params.accessToken = this.accessToken;
      }
      const res = await getDetail(params);
      this.detail = res.data;
    },
    async addComment() {
      const { id, accessToken, textarea } = this;
      await addComment({ id, accesstoken: accessToken, content: textarea });
      this.getTopic();
    },
  },
  async created() {
    this.accessToken = localStorage.getItem('accessToken')
    this.getTopic();
  },
};
</script>

<style lang="less">
.panel-top {
  padding: 10px;
  .detail-html {
    img {
      width: 100%;
    }
  }
}
.add-comment {
  margin: 10px 0 10px 10px;
}
</style>