<template>
  <div v-if="topics.length > 0">
    <TopicsItem v-for="item in topics" :key="item.id" :item="item" />
    <el-pagination
      @current-change="currentChange"
      :current-page.sync="currentPage"
      big
      layout="prev, pager, next"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getTopics } from "../http/api";
import TopicsItem from "../components/TopicsItem.vue";
export default {
  props: ["tab"],
  components: { TopicsItem },
  data() {
    return {
      total: 0,
      topics: [],
      currentPage: 1,
    };
  },
  methods: {
    async currentChange(page) {
      const res = await getTopics({
        params: { tab: this.tab, page, limit: 20 },
      });
      this.topics = res.data;
    },
  },
  watch: {
    tab: {
      immediate: true,
      async handler(tab) {
        const totalItems = [423, 720, 280, 200, 120, 640];
        let index = 0
        if (tab === "good") {
          index = 1;
        } else if (tab === "share") {
          index = 2;
        } else if (tab === "ask") {
          index = 3;
        } else if (tab === "job") {
          index = 4;
        } else if (tab === "dev") {
          index = 5;
        }
        const res = await getTopics({ params: { tab, page: index, limit: 20 } });
        this.total = totalItems[index]
        this.topics = res.data;
        this.currentPage = 1;
      },
    },
  },
};
</script>

<style>
.el-pagination {
  text-align: center;
}
</style>