import Vue from "vue";
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
      userInfor: null 
    },
    mutations: {
      getUserInfor(state, infor) {
        state.userInfor = infor
      }
    },
    actions: {
      getUserInfor({commit}) {
        const userInfor = localStorage.getItem('userInfor')
        if(userInfor) {
          commit('getUserInfor', JSON.parse(userInfor))
        }
      }
    },
    modules:{}
})

export default store
