// 统一管理借口
import http from './http'
// 比如获取文章列表数据
export const getTopics = ({ params }) => http({
    method: 'get',
    url: '/topics',
    // get 请求传 params，post 请求传 data
    params
})
// 获取文章详情
export const getDetail = (params) => http({
    method: 'get',
    url: '/topic/' + params.id,
    params
})

// 验证 accesstoken，登录
export const login = (data) => http({
    method: 'post',
    url: '/accesstoken',
    data
})

// 收藏主题
export const collectTopic = (data) => {
    http({
        method: 'post',
        url: '/topic_collect/collect',
        data
    })
}

// 取消收藏主题
export const deCollectTopic = (data) => http({
    method: 'post',
    url: '/topic_collect/de_collect',
    data
})

// 点赞/取消点赞
export const replyLike = ({ id, accesstoken }) => http({
    method: 'post',
    url: `/reply/${id}/ups`,
    data: { accesstoken }
})

// 添加评论
export const addComment = ({ id, accesstoken, content }) => http({
    method: 'post',
    url: `/topic/${id}/replies`,
    data: { accesstoken, content }
})

// 获取用户信息
export const getUserData = (value) => http({
    method: 'get',
    url: `/user/${value}`
})