<template>
  <div v-if="userData">
    <PanelView>
      <template #header>
        <span>主页/</span>
      </template>
      <template #content>
        <div class="user-top">
          <el-avatar
            shape="square"
            :size="30"
            :src="userData.avatar_url"
          ></el-avatar>
          <span>{{ loginname }}</span>
        </div>
        <div class="user-top">
          <p class="score">{{ userData.score }}积分</p>
          <p class="topic-like-count"></p>
          <div class="address-infor">
            <!-- <p><span>{{userData.githubUsername}}</span></p> -->
            <!-- <p><span>{{}}</span></p>
            <p><span>{{}}</span></p>
            <p><span>{{}}</span></p> -->
          </div>
        </div>
      </template>
    </PanelView>
    <PanelView>
      <template #header>
        <span>最近创建的话题</span>
      </template>
      <template #content>
        <div v-for="item in userData.recent_topics" :key="item.id" class="reply-topics">
          <el-avatar
            shape="square"
            :size="30"
            :src="item.author.avatar_url"
          ></el-avatar>
          <div class="item-infor">
            <router-link to="">{{item.title}}</router-link>
          </div>
        </div>
      </template>
    </PanelView>
    <PanelView>
      <template #header>
        <span>最近创建的话题</span>
      </template>
      <template #content>
        <div v-for="item in userData.recent_replies" :key="item.id" class="reply-topics">
          <el-avatar
            shape="square"
            :size="30"
            :src="item.author.avatar_url"
          ></el-avatar>
          <div class="item-infor">
            <router-link to="">{{item.title}}</router-link>
          </div>
        </div>
      </template>
    </PanelView>
  </div>
</template>

<script>
import PanelView from "../components/PanelView.vue";
import { getUserData } from "../http/api";
export default {
  components: { PanelView },
  props: ["loginname"],
  data() {
    return {
      userData: null,
    };
  },
  watch: {
    loginname: {
      async handler(value) {
        const res = await getUserData(value);
        this.userData = res.data;
      },
      immediate: true,
    },
  },
};
</script>

<style lang="less" scoped>
.user-top {
  display: flex;
}
.reply-topics {
    display: flex;
    padding: 10px;
    background-color:antiquewhite;
    border-top: 1px solid #ccc;
    .el-avatar {
        margin-right: 6px;
    }
    .item-infor{
        a {
            color: blueviolet
        }
    }
}
</style>